/* Modal */
.ReactModalPortal>div {
    opacity: 0;
}

.ReactModalPortal .ReactModal__Overlay {
    align-items: center;
    display: flex;
    justify-content: center;
    transition: opacity .5s ease-in-out;
    z-index: 999;
}

.modal-fondo {
    background-color: rgba(0, 0, 0, 0.7);
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    position: fixed;
}

.ReactModalPortal .ReactModal__Overlay--after-open {
    opacity: 1;
}

.ReactModalPortal .ReactModal__Overlay--before-close {
    opacity: 0;
}

.modal-add-factura {
    border-radius: 5px;
    color: white;
    background: linear-gradient(to bottom, #000000a6, #002536, #002536, #00141de9);
    border: white solid 1px;
    display: inline;
    max-height: 620px;
    max-width: 90vw;
    height: 70vh;
    width: 70vw;
    outline: none;
    padding: 10px;
    overflow-y: auto;

}

