.react-datetime-picker input:focus {
    outline: none;
}

.react-datetime-picker__wrapper {
    border: 0px solid gray !important;
}

.btn-danger {
    background-color: rgba(255, 0, 0, 0.596);
}

.inputfile {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.inputfile+label {
    font-size: 1em;
    font-weight: 700;
    color: white;
    background-color: rgb(85, 77, 77);
    display: inline-block;
    border-radius: 10px;

    :disabled {
        background-color: red;
    }
}

.inputfile:focus+label,
.inputfile+label:hover {
    background-color: darken($color: rgb(85, 77, 77), $amount: 5);
}


.inputfile+label {
    cursor: pointer;
    /* "hand" cursor */
}


.btn-register {
    border: gray solid 1px;
    color: white;
    background-color: gray;
}

.btn-register:hover {
    background-color: rgb(180, 178, 178);
}

.inputfile:focus+label {
    outline: 1px dotted #000;
    outline: -webkit-focus-ring-color auto 5px;
}

.div-list-files {
