.login__main {
    //background-image      : url(../../Pages/Login/assets/fondo-login.png);
    background-color: #181818;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 100vw;
    height: 100vh;
    position: relative;
    overflow: hidden;
    // display            : flex;
    align-items: center;
    justify-content: center;
    color: white
}



.section-logo h1 {
    display: block;
    font-size: 200%;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    margin-bottom: 10px;
}

.section-right-container {
    align-items: center;
    display: flex;
    justify-content: center;
    padding-left: 50px;
    width: 110%;
    min-height: 300px;
    border-left: gray solid 3px
}

.section-right-container h4 {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    // color           : white;
}

.btn-primary {
    font-size: 100%;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    border: black solid 1px;
    color: white !important;
    background-color: gray !important;
}


.btn-primary:hover {
    background-color: darken($color: gray, $amount: 50);
    font-size: 105%;
    border-color: #181818;
    font-weight: bolder;
}

.form-group {
    position: relative;
    z-index: 1;
    margin-bottom: 1rem;
}


.form-group .form-control {
    font-size: 90%;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    box-shadow: none;
    background-color: transparent;

}

input.form-control::placeholder {
    color: rgb(102, 96, 96);
}

input.form-control {
    cursor: text;
    border: none;
    border-bottom: solid rgb(61, 60, 60) 1px;
    border-radius: 0;
    color: white !important;

}

input.form-control:hover {
    border-bottom: gray solid 1px;
    color: white
}



.icon-password:hover {
    color: gray;
    cursor: pointer;
}

.icon-password-active {
    color: white;
    cursor: pointer;
}

.icon-password-active:hover {
    color: gray;
    cursor: pointer;
}

.boton-flotante-login {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 120%;
    z-index: 1;

}

.pop-up-login {
    color: white;
}