.body__dashboard {
    //background-image   : url(../../Pages/Login/assets/fondo-login2.png);
    //background-color:  #00141de9;
    background: linear-gradient(to bottom, #000000a6, #002536, #002536, #00141de9);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    height: 100vh;
    width: 100vw;
    display: flex;
    color: black;
    padding-bottom: 50px;
    overflow-y: auto;
    color: white
}


.table__facturas {
    color: white;
    max-height: 85vh;
    max-width: 80vw;
}

.container-add-button {
    position: fixed;
    bottom: 30px;
    left: 30px;
    color: black;
    font-size: 200%;
    width: 90px;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;

}

.btn-factura-add {
    border-radius: 100%;
    background-color: #00021a69 !important;
    box-shadow: 5px 5px 3px 0px rgb(5, 5, 5);
    width: 70px;
    height: 70px;
    border: none !important
}

.btn-factura-add:hover {
    color: rgb(131, 130, 130);
    border-radius: 100%;
    background-color: darken($color: #00021a69, $amount: 10) !important;
    font-size: 110%;
    box-shadow: 7px 7px 5px 3px rgb(0, 0, 0);
    width: 80px;
    height: 80px;
}

.card-dashboard {
    display: block;
    border-radius: 10px;
    box-shadow: 0px 0px 10px 0px rgb(0, 0, 0);
    min-height: auto;

}

.bg-gradient1 {
    background: linear-gradient(to right, rgba(4, 4, 91, 0.691), rgb(6, 109, 160), rgba(5, 166, 246, 0.528));
    border-radius: 20%;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.bg-gradient2 {
    background: linear-gradient(to right, rgba(110, 13, 2, 0.685), rgba(140, 4, 4, 0.528), rgba(234, 7, 7, 0.522));
    border-radius: 20%;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.bg-gradient3 {
    background: linear-gradient(to right, rgba(0, 97, 58, 0.691), rgb(1, 144, 99), rgba(2, 250, 221, 0.527));
    border-radius: 20%;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.bg-gradient4 {
    background: linear-gradient(to right, rgba(59, 4, 91, 0.729), rgba(102, 6, 157, 0.729), rgba(159, 9, 247, 0.729));
    border-radius: 20%;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.prod-class:hover {
    background-color: black;
}

.table-min{
    min-width: 500px;
}
.btn-cerrar{
    position: fixed;
    right: 150px;
    top: 70px;
}

.mw-90vh{
    min-height: 80vh;
}
