.loading {
    width              : 100vw;
    margin             : 0px;
    height             : 100vh;
    background-size    : cover;
    background-repeat  : no-repeat;
    background-position: center;
    background-image   : url(../../Pages/Login/assets/fondo-login2.png);
}

.loading-dashboard {
    width           : 100vw;
    margin          : 0px;
    height          : 100vh;
    background-color: transparent;
}

.sk-chase {
    width          : 40px;
    height         : 40px;
    // margin-top: 90vh;
    //  margin-left: 90%;
    position       : relative;
    animation      : sk-chase 3.0s infinite linear both;
}

.sk-chase-sm {
    width          : 20px;
    height         : 20px;
    // margin-top: 90vh;
    //  margin-left: 90%;
    position       : relative;
    animation      : sk-chase 3.0s infinite linear both;
}

.sk-chase-dot {
    width    : 100%;
    height   : 100%;
    position : absolute;
    left     : 0;
    top      : 0;
    animation: sk-chase-dot 2.0s infinite ease-in-out both;
}

.sk-chase-dot:before {
    content         : '';
    display         : block;
    width           : 25%;
    height          : 25%;
    background-color:rgb(252, 252, 252);
    border-radius   : 100%;
    animation       : sk-chase-dot-before 2.0s infinite ease-in-out both;
}

.sk-chase-dot:nth-child(1) {
    animation-delay: -1.1s;
}

.sk-chase-dot:nth-child(2) {
    animation-delay: -1.0s;
}

.sk-chase-dot:nth-child(3) {
    animation-delay: -0.9s;
}

.sk-chase-dot:nth-child(4) {
    animation-delay: -0.8s;
}

.sk-chase-dot:nth-child(5) {
    animation-delay: -0.7s;
}

.sk-chase-dot:nth-child(6) {
    animation-delay: -0.6s;
}

.sk-chase-dot:nth-child(1):before {
    animation-delay: -1.1s;
}

.sk-chase-dot:nth-child(2):before {
    animation-delay: -1.0s;
}

.sk-chase-dot:nth-child(3):before {
    animation-delay: -0.9s;
}

.sk-chase-dot:nth-child(4):before {
    animation-delay: -0.8s;
}

.sk-chase-dot:nth-child(5):before {
    animation-delay: -0.7s;
}

.sk-chase-dot:nth-child(6):before {
    animation-delay: -0.6s;
}

@keyframes sk-chase {
    100% {
        transform: rotate(360deg);
    }
}

@keyframes sk-chase-dot {

    80%,
    100% {
        transform: rotate(360deg);
    }
}

@keyframes sk-chase-dot-before {
    50% {
        transform: scale(0.4);
    }

    100%,
    0% {
        transform: scale(1.0);
    }
}

$w     : 96px;
$h     : 112px;
$xspace: $w/2;
$yspace: $h/4 - 1;
$speed : 1.5s;

.tetrominos {
    position : absolute;
    top      : 50%;
    left     : 50%;
    transform: translate(-$h, -$w);

}

.tetromino {
    width     : $w;
    height    : $h;
    position  : absolute;
    transition: all ease .3s;
    background: url('data:image/svg+xml;utf-8,%3Csvg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 612 684"%3E%3Cpath fill="%2301307CAA" d="M305.7 0L0 170.9v342.3L305.7 684 612 513.2V170.9L305.7 0z"/%3E%3Cpath fill="%23001535" d="M305.7 80.1l-233.6 131 233.6 131 234.2-131-234.2-131"/%3E%3C/svg%3E') no-repeat top center;
}

.box1 {
    animation: tetromino1 $speed ease-out infinite;
}

.box2 {
    animation: tetromino2 $speed ease-out infinite;
}

.box3 {
    animation: tetromino3 $speed ease-out infinite;
    z-index  : 2;
}

.box4 {
    animation: tetromino4 $speed ease-out infinite;
}

@keyframes tetromino1 {

    0%,
    40% {
        /* compose logo */
        /* 1 on 3 */
        /* L-shape */
        transform: translate(0, 0);
    }

    50% {
        /* pre-box */
        transform: translate($xspace, -$yspace);
    }

    60%,
    100% {
        /* box */
        /* compose logo */
        transform: translate($xspace*2, 0);
    }
}

@keyframes tetromino2 {

    0%,
    20% {
        /* compose logo */
        /* 1 on 3 */
        transform: translate($xspace*2, 0px);
    }

    40%,
    100% {
        /* L-shape */
        /* box */
        /* compose logo */
        transform: translate($xspace*3, $yspace);
    }
}

@keyframes tetromino3 {
    0% {
        /* compose logo */

        transform: translate($xspace*3, $yspace);
    }

    20%,
    60% {
        /* 1 on 3 */
        /* L-shape */
        /* box */
        transform: translate($xspace*2, $yspace*2);
    }

    90%,
    100% {
        /* compose logo */
        transform: translate($xspace, $yspace);
    }
}

@keyframes tetromino4 {

    0%,
    60% {
        /* compose logo */
        /* 1 on 3 */
        /* L-shape */
        /* box */
        transform: translate($xspace, $yspace);
    }

    90%,
    100% {
        /* compose logo */
        transform: translate(0, 0);
    }
}

.mt-20 {
    margin-top: 200px;
}